/*btn*/
[class|="btn"],
[class*=" btn-"],
input[type="submit"],
input[type="button"],
input[type="reset"],
button {
  display: inline-block;
  vertical-align: top;
  position: relative;
  text-align: center;
  text-decoration: none;
  box-sizing: border-box;
}

[class|="btn"] input[type="submit"],
[class*=" btn-"] input[type="submit"],
[class|="btn"] input[type="button"],
[class*=" btn-"] input[type="button"],
[class|="btn"] input[type="reset"],
[class*=" btn-"] input[type="reset"] {
  background: url("../img/empty.png") repeat;
  cursor: pointer;
  display: block;
  height: 100%;
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
  border: 0;
  opacity: 0;
}

/*btn-default*/
.btn-default,
input[type="submit"],
input[type="button"],
input[type="reset"],
button {
  font-family: 'rotondac-bold', Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 30px;
  color: #00abc7;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  padding: 10px 34px;
  border: 1px solid transparent;
  background: #fcd105;
  box-shadow: 0 0 0 #fde46a;
  border-radius: 25px;
  transition: color 0.3s, background-color 0.3s, box-shadow 0.3s;
  position: relative;
  box-sizing: border-box;
}

@media (max-width: 767px) {
  .btn-default,
  input[type="submit"],
  input[type="button"],
  input[type="reset"],
  button {
    padding: 8px 30px;
  }
}

.desktop .btn-default:hover, .desktop
input[type="submit"]:hover, .desktop
input[type="button"]:hover, .desktop
input[type="reset"]:hover, .desktop
button:hover {
  background-color: #fde46a;
  box-shadow: 0 0 20px #fde46a;
}

