.csstransforms .fotorama__arr:before,
.csstransforms .fotorama__arr:after {
  content: '';
  position: absolute;
  left: 6px;
  top: 50%;
  height: 6px;
  width: 20px;
  border-radius: 5px;
  background: #00abc7;
}

.csstransforms .fotorama__arr:before {
  margin: -8px 0 0;
}

.csstransforms .fotorama__arr:after {
  margin: 2px 0 0;
}

.csstransforms .fotorama__arr--prev:before {
  transform: rotate(-45deg);
}

.csstransforms .fotorama__arr--prev:after {
  transform: rotate(45deg);
}

.csstransforms .fotorama__arr--next:before {
  transform: rotate(45deg);
}

.csstransforms .fotorama__arr--next:after {
  transform: rotate(-45deg);
}

.csstransforms .fotorama__fullscreen-icon {
  width: 22px;
  height: 22px;
  top: 10px;
  right: 10px;
  overflow: hidden;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 2px;
}

.csstransforms .fotorama__fullscreen-icon:before {
  content: '';
  position: absolute;
  left: -8px;
  top: -8px;
  right: -8px;
  bottom: -8px;
  border-radius: 0;
  border: solid #00abc7;
  border-width: 12px 0;
  transform: rotate(45deg);
}

.csstransforms .fotorama__fullscreen-icon:after {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  height: 4px;
  width: 20px;
  margin: -3px 0 0 2px;
  border-radius: 0;
  background: #00abc7;
  transform: rotate(-45deg);
}

.csstransforms .fotorama--fullscreen .fotorama__fullscreen-icon:before {
  left: 50%;
  top: 50%;
  right: auto;
  bottom: auto;
  width: 0;
  height: 0;
  margin: -8px 0 0 -8px;
  border-radius: 0;
  border: 8px solid;
  border-color: #00abc7 transparent;
}

.csstransforms .fotorama--fullscreen .fotorama__fullscreen-icon:after {
  content: none;
}

