@import "functions";
@import "placeholders";

// Media query
// ====================================================
$breakpoints: (
    "desk-l": 2560px,
    "desk": 1920px,
    "desk-s": 1600px,
    "lap-l": 1440px,
    "lap": 1366px,
    "lap-s": 1280px,
    "net": 1200px,
    "tab": 992px,
    "tab-s": 768px,
    "mob": 640px,
    "mob-s": 480px,
    "mob-xs": 375px,
    "mob-xxs": 360px
);


// Fonts style
// ====================================================
$font-norm: 'helvetica-neue-cyr-light', Arial, Helvetica, sans-serif !default;
$font-bold: 'helvetica-neue-cyr-bold', Arial, Helvetica, sans-serif !default;
$font-italic: 'helvetica-neue-cyr-light-italic', Arial, Helvetica, sans-serif !default;
$font-bold-italic: 'helvetica-neue-cyr-bold-italic', Arial, Helvetica, sans-serif !default;
$font-thin: 'helvetica-neue-cyr-thin-italic', Arial, Helvetica, sans-serif !default;
$font-alt: 'rotondac-regular', Arial, Helvetica, sans-serif !default;
$font-alt-bold: 'rotondac-bold', Arial, Helvetica, sans-serif !default;

// Main font
// ----------------------------------------------------
@mixin font-normal {
  font-family: $font-norm;
}

@mixin font-bold {
  font-family: $font-bold;
  font-weight: normal;
}

@mixin font-italic {
  font-family: $font-italic;
  font-style: normal;
}

@mixin font-bold-italic {
  font-family: $font-bold-italic;
  font-weight: normal;
  font-style: normal;
}

@mixin font-thin {
  font-family: $font-thin;
  font-weight: normal;
}

// Alternative font
// ----------------------------------------------------
@mixin font-alt {
  font-family: $font-alt;
}

@mixin font-alt-bold {
  font-family: $font-alt-bold;
  font-weight: normal;
}

@mixin font-alt-italic {
  font-family: $font-alt;
  font-style: italic;
}

@mixin font-alt-bold-italic {
  font-family: $font-alt-bold;
  font-weight: normal;
  font-style: italic;
}


// Base font size
// ====================================================
$font-size: 18px !default;
$line-height: 32px !default;
$letter-spacing: normal !default;
// ------ mob -----------------------------------------
$font-size--mob: 16px !default;
$line-height--mob: 25px !default;


// Heading h1
// ====================================================
$font-size-h1: 4.2rem !default;
$line-height-h1: 5.2rem !default;
$line-height-h1-kef: $line-height-h1 / $font-size-h1;
// ------ tab -----------------------------------------
$font-size-h1--tab: 3.4rem !default;
$line-height-h1--tab: round-float($font-size-h1--tab * $line-height-h1-kef, 1) !default; //   ,    
// ------ mob -----------------------------------------
$font-size-h1--mob: 3.0rem !default;
$line-height-h1--mob: round-float($font-size-h1--mob * $line-height-h1-kef, 1) !default; //   ,    
// ----------------------------------------------------
@mixin font-h1() {
  font-size: $font-size-h1;
  line-height: $line-height-h1;

  @include media("<tab") {
    font-size: $font-size-h1--tab;
    line-height: $line-height-h1--tab;
  }

  @include media("<mob") {
    font-size: $font-size-h1--mob;
    line-height: $line-height-h1--mob;
  }
}


// Heading h2
// ====================================================
$font-size-h2: 3.0rem !default;
$line-height-h2: 4.2rem !default;
$line-height-h2-kef: $line-height-h2 / $font-size-h2;
// ------ tab -----------------------------------------
$font-size-h2--tab: 2.8rem !default;
$line-height-h2--tab: round-float($font-size-h2--tab * $line-height-h2-kef, 1) !default; //   ,    
// ------ mob -----------------------------------------
$font-size-h2--mob: 2.4rem !default;
$line-height-h2--mob: round-float($font-size-h2--mob * $line-height-h2-kef, 1) !default; //   ,    
// ----------------------------------------------------
@mixin font-h2() {
  font-size: $font-size-h2;
  line-height: $line-height-h2;

  @include media("<tab") {
    font-size: $font-size-h2--tab;
    line-height: $line-height-h2--tab;
  }

  @include media("<mob") {
    font-size: $font-size-h2--mob;
    line-height: $line-height-h2--mob;
  }
}


// Heading h3
// ====================================================
$font-size-h3: 2.4rem !default;
$line-height-h3: 3.0rem !default;
$line-height-h3-kef: $line-height-h3 / $font-size-h3;
// ------ tab -----------------------------------------
$font-size-h3--tab: 2.2rem !default;
$line-height-h3--tab: round-float($font-size-h3--tab * $line-height-h3-kef, 1) !default; //   ,    
// ------ mob -----------------------------------------
$font-size-h3--mob: 1.8rem !default;
$line-height-h3--mob: round-float($font-size-h3--mob * $line-height-h3-kef, 1) !default; //   ,    
// ----------------------------------------------------
@mixin font-h3() {
  font-size: $font-size-h3;
  line-height: $line-height-h3;

  @include media("<tab") {
    font-size: $font-size-h3--tab;
    line-height: $line-height-h3--tab;
  }

  @include media("<mob") {
    font-size: $font-size-h3--mob;
    line-height: $line-height-h3--mob;
  }
}


// Heading h4
// ====================================================
$font-size-h4: 2.0rem !default;
$line-height-h4: 2.8rem !default;
// ------ mob -----------------------------------------
$font-size-h4--mob: $font-size--mob !default;
$line-height-h4--mob: $line-height--mob !default;
// ----------------------------------------------------
@mixin font-h4() {
  font-size: $font-size-h4;
  line-height: $line-height-h4;

  @include media("<mob") {
    font-size: $font-size-h4--mob;
    line-height: $line-height-h4--mob;
  }
}


// Heading h5
// ====================================================
$font-size-h5: $font-size !default;
$line-height-h5: $line-height !default;
// ----------------------------------------------------
@mixin font-h5() {
  font-size: $font-size-h5;
  line-height: $line-height;
}


// Heading h6
// ====================================================
$font-size-h6: round-float($font-size * 0.70, 1) !default;
$line-height-h6: round-float($line-height * 0.95, 1) !default;

@mixin font-h6() {
  font-size: $font-size-h6;
  line-height: $line-height-h6;
}


// Spaces
// ====================================================
$bottom-space: $line-height !default;
$bottom-section: round-float($line-height * 1.5, 1) !default;
// ------ mob -----------------------------------------
$bottom-space--mob: $line-height--mob !default;
$bottom-section--mob: round-float($line-height--mob * 1.5, 1) !default;


// Colors
// ====================================================
$link-color: #00abc7 !default;
$focus-color: #0080ff !default;
$success-color: #28a745 !default;
$error-color: #dc3545 !default;
$info-color: #17a2b8 !default;
$warning-color: #ffc107 !default;
$light-color: #f8f9fa !default;
$dark-color: #343a40 !default;
// ----------------------------------------------------
$base-color: #00abc7 !default;
$base-color-alt: #fcd105 !default;
// ----------------------------------------------------
$highlight-color: #fcd105 !default
$highlight-color--hover: lighten($base-color-alt, 20%) !default
$highlight-color-alt: $base-color !default
$highlight-color-alt--hover: lighten($highlight-color-alt, 20%) !default
// ----------------------------------------------------
$bg-color: #fff !default;
$bg-color-alt: #f3f3f3 !default;
// ----------------------------------------------------
$text-color: #4c4c4c !default;
$text-color-muted: #6c757d !default;
$text-color-light: #ccc !default;
// ----------------------------------------------------
$title-color: #333 !default;
$title-color-alt: $base-color !default;
// ----------------------------------------------------
$border-color: #ccc !default;
// ----------------------------------------------------
$mark-color: $info-color !default;


// Layout page
// ====================================================
//   
$layout-page-min-width: 320px !default;
//   
$layout-page-max-width: 2560px !default;
//    
$layout-page-space: 30px 30px !default;
$layout-page-space--tab: 30px 25px !default; // <992px
$layout-page-space--mob: 20px 20px !default; // <640px


// Layout text
// ====================================================
//   
$layout-text-width: 800px !default;
//   
$layout-text-space: 20px 30px !default;
//       
$layout-flood-width: 1120px !default;

$max-wrap-width: 1120px;
$max-wrap-width--lap: 980px;

// Text page intends
// ====================================================
$text-page-intend-left: 280px;
$text-page-intend-left--lap: 200px;
$text-page-intend-left--net: 180px;
$text-page-intend-left--tab: nth($layout-page-space--tab, 2);
$text-page-intend-left--mob: nth($layout-page-space--mob, 2);
// ----------------------------------------------------
$text-page-intend-right: 80px;
$text-page-intend-right--lap: nth($layout-page-space, 2);
$text-page-intend-right--net: nth($layout-page-space, 2);
$text-page-intend-right--tab: nth($layout-page-space--tab, 2);
$text-page-intend-right--mob: nth($layout-page-space--mob, 2);


// Inner space
// ====================================================
//  
$grid-space: 28px 30px !default;


// Buttons
// ====================================================
$btn-height: 5rem !default;
$btn-bg-color: $base-color !default;
$btn-bg-hover-color: invert($base-color) !default;
$btn-text-color: white !default;
$btn-disabled-color: #ced4da !default;

@mixin btn-disabled() {
  cursor: default !important;
  color: white !important;
  border-color: $btn-disabled-color !important;
  background-color: $btn-disabled-color !important;
  box-shadow: none !important;
  pointer-events: none !important;
}


// ====================================================
// FORM ELEMENTS
// ====================================================

// Form elements media query
// ====================================================
$form-media--mob: '<mob' !default;


// Form elements external indents
// ====================================================
$form-bottom-space: 3rem !default;
// ------ mob -----------------------------------------
$form-bottom-space--mob: 2.5rem !default;

// Form elements vertical space mixin
// ----------------------------------------------------
@mixin form-def-space {
  margin: 0 0 $form-bottom-space;
}

// ------ mob -----------------------------------------
@mixin form-def-space--mob {
  margin: 0 0 $form-bottom-space--mob;
}


// ====================================================
// INPUT
// ====================================================

// Form elements colors scheme
// ====================================================
$form-def-text-color: $text-color !default;
$form-def-placeholder-color: lighten($form-def-text-color, 65%) !default;
$form-def-bg-color: #fff !default;
$form-def-border-color: lighten($form-def-text-color, 70%) !default;
// ----------------------------------------------------
$form-def-border-color--focus: darken($form-def-border-color, 20%) !default;
// ----------------------------------------------------
$form-error-color: $error-color !default;
$form-success-color: $success-color !default;
// ----------------------------------------------------
$form-text-color--disabled: #ced4da !default;
$form-bg-color--disabled: #e9ecef !default;
$form-border-color--disabled: #ced4da !default;


// Form elements disabled mixin
// ====================================================
@mixin forms-disabled {
  cursor: default !important;
  color: $form-text-color--disabled !important;
  background-color: $form-bg-color--disabled !important;
  border-color: $form-border-color--disabled !important;
  box-shadow: none !important;
}


// Form elements fonts styles
// ====================================================
$form-def-font-size: 1.6rem !default;
$form-def-line-height: 2.4rem !default;
$form-def-letter-spacing: null !default;
// ------ mob -----------------------------------------
$form-def-font-size--mob: 1.4rem !default;
$form-def-line-height--mob: 1.8rem !default;
$form-def-letter-spacing--mob: null !default;


// Form elements layout
// ====================================================
$form-def-height: 4.8rem !default;
$form-def-border-width: 1px !default;
$form-def-space-v: round-float(($form-def-height - $form-def-line-height) / 2 - px-to-rem($form-def-border-width), 1) !default; // ,         
$form-def-space-vt: $form-def-space-v !default;
$form-def-space-vb: $form-def-space-v !default;
$form-def-space-h: 1.8rem !default;
$form-def-border-radius: 5px !default;
// ------ mob -----------------------------------------
$form-def-height--mob: 4.2rem !default;
$form-def-border-width--mob: 1px !default;
$form-def-space-v--mob: round-float(($form-def-height--mob - $form-def-line-height--mob) / 2 - px-to-rem($form-def-border-width--mob), 1) !default; // ,         
$form-def-space-vt--mob: $form-def-space-v--mob !default;
$form-def-space-vb--mob: $form-def-space-v--mob !default;
$form-def-space-h--mob: 1.4rem !default;
$form-def-border-radius--mob: 5px !default;


// Input common styles mixin
// ====================================================
@mixin input-common {
  display: block;
  position: relative;
  height: $form-def-height;
  width: 100%;
  padding: $form-def-space-vt $form-def-space-h $form-def-space-vb $form-def-space-h;
  background-color: $form-def-bg-color;
  box-shadow: none;
  @if $form-def-border-width {
    border: $form-def-border-width solid $form-def-border-color;
  }
}

// ------ mob -----------------------------------------
@mixin input-common--mob {
  height: $form-def-height--mob;
  padding: $form-def-space-vt--mob $form-def-space-h--mob $form-def-space-vb--mob $form-def-space-h--mob;
  border-radius: $form-def-border-radius--mob;
  @if $form-def-border-width--mob {
    border-width: $form-def-border-width--mob;
  }
}

// Input font styles mixin
// ====================================================
@mixin input-font {
  @include font-normal;
  font-size: $form-def-font-size;
  line-height: $form-def-line-height;
  color: $form-def-text-color;
  @if $form-def-letter-spacing {
    letter-spacing: $form-def-letter-spacing;
  }
}

// ------ mob -----------------------------------------
@mixin input-font--mob {
  font-size: $form-def-font-size--mob;
  line-height: $form-def-line-height--mob;
  @if $form-def-letter-spacing--mob {
    letter-spacing: $form-def-letter-spacing--mob;
  }
}


// ====================================================
// SELECT
// ====================================================

//        

// Select colors scheme
// ====================================================
$select-text-color: $form-def-text-color !default;
$select-placeholder-color: $form-def-placeholder-color !default;
$select-bg-color: $form-def-bg-color !default;
$select-border-color: $form-def-border-color !default;
// ----------------------------------------------------
$select-border-color--focus: $form-def-border-color--focus !default;


// Select fonts styles
// ====================================================
$select-font-size: $form-def-font-size !default;
$select-line-height: $form-def-line-height !default;
$select-letter-spacing: $form-def-letter-spacing !default;
// ------ mob -----------------------------------------
$select-font-size--mob: $form-def-font-size--mob !default;
$select-line-height--mob: $form-def-line-height--mob !default;
$select-letter-spacing--mob: $form-def-letter-spacing--mob !default;


// Select layout
// ====================================================
$select-height: $form-def-height !default;
$select-border-width: $form-def-border-width !default;
$select-space-v: round-float(($select-height - $select-line-height) / 2 - px-to-rem($select-border-width), 1) !default; // ,         
$select-space-vt: $select-space-v !default;
$select-space-vb: $select-space-v !default;
$select-space-h: $form-def-space-h - 0.4 !default;
$select-border-radius: $form-def-border-radius !default;
$select-arrow-width: 4.0rem !default;
// ------ mob -----------------------------------------
$select-height--mob: $form-def-height--mob !default;
$select-border-width--mob: $form-def-border-width--mob !default;
$select-space-v--mob: round-float(($select-height--mob - $select-line-height--mob) / 2 - px-to-rem($select-border-width), 1) !default; // ,         
$select-space-vt--mob: $select-space-v--mob !default;
$select-space-vb--mob: $select-space-v--mob !default;
$select-space-h--mob: $form-def-space-h--mob - 0.4 !default;
$select-border-radius--mob: $form-def-border-radius--mob !default;
$select-arrow-width--mob: 4.0rem !default;


// Select common styles mixin
// ====================================================
@mixin select-common() {
  display: block;
  position: relative;
  height: $select-height;
  width: 100%;
  padding: $select-space-vt $select-arrow-width $select-space-vb $select-space-h;
  background: $select-bg-color;
  border: #{$select-border-width} solid #{$select-border-color};
  border-radius: 5px;
  box-shadow: none;
  text-indent: 0;
  outline: none;
  cursor: pointer;
  user-select: none;
}

// ------ mob -----------------------------------------
@mixin select-common--mob() {
  height: $select-height--mob;
  padding: $select-space-vt--mob $select-arrow-width--mob $select-space-vb--mob $select-space-h--mob;
  border: #{$select-border-width--mob} solid #{$select-border-color};
}


// Select font styles mixin
// ====================================================
@mixin select-font {
  @include input-font;
}

// ------ mob -----------------------------------------
@mixin select-font--mob {
  @include input-font--mob;
}


// ====================================================
// FORM STATES
// ====================================================
@mixin form-success {
  color: $form-success-color;
  border-color: $form-success-color;
}

@mixin form-error {
  color: $form-error-color;
  border-color: $form-error-color;
}

@mixin filled {
  color: slateblue;
  border-color: slateblue;
}

@mixin focused {
  border-color: $form-def-border-color--focus;
}


// Form validate feedback
// ====================================================
$validate-feedback-font-size: 1.2rem;
$validate-feedback-line-height: 1.6rem;
$validate-feedback-height: 2.2rem;


// Animation
// ====================================================
$animation-speed: 0.3s !default;
$animation-speed-mouseleave: 0.35s !default;


// Header
// ====================================================
$header-height: 86px;
$header-height--tab: 60px; // 992px '<tab'
$header-height--mob: 50px; // 640px '<mob'

$header-btn-height: 62px;
$header-btn-height--tab: 46px; // <992px
$header-btn-height--mob: 42px; // <640px


// Filters
// ====================================================
$filters-panel-height: 100px !default;
$filters-panel-height--desk-s: 72px !default;
$filters-panel-height--lap-l: 144px !default;
$filters-panel-height--lap-s: 144px !default;
$filters-panel-height--tab: 231px !default;
$filters-panel-height--tab-s: 166px !default;
$filters-panel-height--mob-s: 140px !default;

$filters-drop-group-space: 40px 30px !default;

$filter-btn-arr-size: 34px;
$filter-btn-arr-size--tab: 26px;
$filter-btn-spaces: 17px 18px;
$filter-btn-spaces--tab: 9px 10px;
$filter-btn-spaces--tab-s: 12px 20px;

@mixin filter-btn-font {
  @include font-alt-bold;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 0.08em;
  white-space: nowrap;
  color: white;
  text-align: center;
  text-decoration: none;
  text-transform: none;
}

@mixin filter-btn-font--tab {
  font-size: 15px;
  line-height: 24px;
}

@mixin filter-btn-font--tab-s {
  font-size: 18px;
  line-height: 24px;
  color: $base-color;
}


// Sidebar
// ====================================================
$sidebar-width: 240px;


// Footer
// ====================================================


// Z-index
// ====================================================
$sidebar-z: 65 !default; //    
$sidebar-z--tab: 84 !default; //    
$mob-menu-overlay-z: 83 !default; //    

$header-z: 90 !default; // 

$menu-z: 95 !default; //  

$promo-z: 75 !default; // 
$promo-overlay-z: 70 !default; // 

$popup-z: 85 !default; //    


// box-shadow
// ====================================================
$box-shadow: 0 2px 20px rgba(black, 0.3) !default;